        /** baseURL */
        this.baseURL = "";
    }
    onWatchBaseURL() {
        axios.defaults.withCredentials = this.baseURL.length == 0;
        axios.defaults.baseURL = this.baseURL;
    }
    created() {
        if (process.env.NODE_ENV == 'production') {
            axios.defaults.withCredentials = false;
            if (!this.baseURL)
                this.baseURL = "http://192.168.0.199:8099";
            return;
        }
    }
};
